@use "_variables";
$blue: #002e58;
$blue-dark: #00315c;
$blue-light: #003d73;
$gray: #eeeeee;
$gray-dark: #cccccc;
$resume-bg: $gray;
$bg: black;

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: #000;
  transition: all 1s;
}
